@import url("https://fonts.googleapis.com/css2?family=Nunito:ital,wght@0,200;0,400;0,700;1,500&display=swap");

* {
  font-family: "Nunito", sans-serif;
}
html,
body {
  margin: 0;
  padding: 0;
  overflow-x: hidden;
}
.text-center {
  text-align: center;
}
.text-right {
  text-align: right;
}
.text-left {
  text-align: left;
}

.btn {
  border: none;
  cursor: pointer;
  background-color: transparent;
}

.full-h {
  height: 100%;
}
.row {
  display: flex;
  flex-direction: row;
  flex-wrap: 100%;
  width: 100%;
}
.col {
  display: flex;
  flex-direction: column;
  flex-basis: 100%;
  flex: 1;
  grid-column: span 1 / span 1;
}
