@media only screen and (max-width: 600px) {
  .logo-grid {
    display: none;
  }
  .logo-mobile-grid {
    display: inline-block !important;
    span#logo-mobile {
      font-size: 20px;
    }
  }
}

header {
  width: 100%;
  padding: 20px 0px;
  background-color: #fff;

  .logo-mobile-grid {
    display: none;
  }

  span#logo,
  span#logo-mobile {
    display: block;
    font-weight: bold;
    margin-top: 8px;
    color: #6aa4df;
  }

  #say-hello-btn {
    background: #000;
    color: #fff;
    font-size: 14;
    text-transform: none;
    border-radius: 20px;
    box-shadow: none;
  }
}
