@media only screen and (max-width: 900px) and (min-width: 601px) {
  img#photo {
    width: 200px !important;
  }
}

@media only screen and (max-width: 600px) {
  #landing {
    .landing-subcontainer {
      margin-top: 0 !important;
    }

    .landing-title {
      h1 {
        font-size: 30px;
      }
      h2 {
        font-size: 16px;
      }
      h3 {
        font-size: 14px;
      }
      button#resume-btn {
        display: none;
      }
    }

    .landing-photo {
      img {
        width: 30vh;
        padding-top: 10vh;
      }
      button#resume-mobile-btn {
        display: flex;
        margin: 20px auto 0px auto;
      }
    }
  }
}

@media only screen and (max-height: 740px) {
  img#photo {
    padding-top: 2vh !important;
  }
}

#landing {
  height: 100vh;

  .landing-subcontainer {
    padding-top: 10%;
  }

  h3,
  h2 {
    font-weight: normal;
    font-family: "Nunito", sans-serif;
    b {
      font-family: "Nunito", sans-serif;
    }
  }

  button#resume-btn,
  button#resume-mobile-btn {
    text-transform: capitalize;
    width: 25%;
    border-radius: 20px;
    border-bottom: 4px solid #000;
    color: black;
    margin-top: 30px;
    padding-left: 30px;
    padding-right: 30px;
    border-color: black;
  }

  button#resume-mobile-btn {
    display: none;
  }

  h1 {
    font-size: 60px;
    margin: 0;
  }

  .divider {
    position: absolute;
    left: 0;
    bottom: 0;
    width: 100%;
    height: 2px;
    margin-bottom: 35px;
    background-color: black;
  }

  #social-medias-block {
    position: absolute;
    z-index: 1;
    left: 0;
    bottom: 0;
    width: 100%;
    margin-bottom: 10px;
    text-align: center;
    ul {
      list-style: none;
      padding: 0;
      li {
        display: inline-block;
        padding: 0px 10px;
        background-color: white;
        a {
          text-decoration: none;
          color: black;
        }
      }
    }
  }

  img {
    border-radius: 100%;
  }
}
