@media only screen and (max-width: 900px) {
  #list-component {
    .project-tech-filter {
      border-left: 1px solid #000 !important;
    }
    .filter-button-grid {
      border-right: 1px solid #000;
    }
    .empty-grid {
      display: none;
    }
  }
}

#list-component {
  width: 100%;
  padding-top: 40px;
  margin-bottom: 150px;

  h2 {
    font-size: 30px;
  }

  .list-project {
    text-align: center;
    padding-bottom: 100px;

    .slider-item {
      width: 100%;
      padding-bottom: 15px;
      border-radius: 10px;
      transition: all 0.7s;

      .project-logo {
        width: 100%;
        height: 200px;
        margin-bottom: 10px;
        border-radius: 10px 10px 10px 10px;
      }

      .project-title {
        font-size: 16px;
        padding: 10px 5px 0px 5px;
      }

      .project-description {
        overflow: scroll;
        width: 90%;
        height: 50px;
        resize: none;
        padding: 4px;
        border: none;
        border-radius: 6px;
        margin-bottom: 6px;
      }

      .project-description:focus {
        outline: none;
      }

      .project-chips {
        width: 100%;
        height: 80px;
        overflow: scroll;
        margin: 15px 0px;
      }
    }
  }
}
